import { useState, useEffect, useCallback } from 'react';

interface TimeZoneInfo {
  timeZone: string;
  offset: number;
  changed: boolean;
}

/**
 * ÏãúÍ∞ÑÎåÄ Î≥ÄÍ≤ΩÏùÑ Í∞êÏßÄÌïòÎäî Ïª§Ïä§ÌÖÄ ÌõÖ
 * Îß•ÏóêÏÑú ÏãúÍ∞ÑÎåÄ Î≥ÄÍ≤Ω Ïãú Ïã§ÏãúÍ∞ÑÏúºÎ°ú Í∞êÏßÄÎê©ÎãàÎã§
 */
export function useTimeZoneDetection() {
  const [timeZoneInfo, setTimeZoneInfo] = useState<TimeZoneInfo>(() => {
    if (typeof window === 'undefined') {
      console.log('üåç ÏÑúÎ≤Ñ ÏÇ¨Ïù¥Îìú: UTCÎ°ú Ï¥àÍ∏∞Ìôî');
      return {
        timeZone: 'UTC',
        offset: 0,
        changed: false,
      };
    }

    const timeZone = Intl.DateTimeFormat().resolvedOptions().timeZone;
    const offset = new Date().getTimezoneOffset();
    
    console.log('üåç ÌÅ¥ÎùºÏù¥Ïñ∏Ìä∏ Ï¥àÍ∏∞Ìôî:', {
      Í∞êÏßÄÎêú_ÏãúÍ∞ÑÎåÄ: timeZone,
      Ïò§ÌîÑÏÖã: offset,
      ÌòÑÏû¨_ÏãúÍ∞Ñ: new Date().toLocaleString()
    });
    
    return {
      timeZone,
      offset,
      changed: false,
    };
  });

  const checkTimeZone = useCallback(() => {
    if (typeof window === 'undefined') return;

    try {
      const currentTimeZone = Intl.DateTimeFormat().resolvedOptions().timeZone;
      const currentOffset = new Date().getTimezoneOffset();
      
      // ÎîîÎ≤ÑÍπÖÏùÑ ÏúÑÌïú Î°úÍ∑∏
      console.log('üîç ÏãúÍ∞ÑÎåÄ Ï≤¥ÌÅ¨:', {
        ÌòÑÏû¨_Í∞êÏßÄÎêú_ÏãúÍ∞ÑÎåÄ: currentTimeZone,
        ÌòÑÏû¨_Ïò§ÌîÑÏÖã: currentOffset,
        Ï†ÄÏû•Îêú_ÏãúÍ∞ÑÎåÄ: timeZoneInfo.timeZone,
        Ï†ÄÏû•Îêú_Ïò§ÌîÑÏÖã: timeZoneInfo.offset,
        Î≥ÄÍ≤ΩÎê®: currentTimeZone !== timeZoneInfo.timeZone || currentOffset !== timeZoneInfo.offset
      });
      
      // ÏãúÍ∞ÑÎåÄ ÎòêÎäî Ïò§ÌîÑÏÖãÏù¥ Î≥ÄÍ≤ΩÎêòÏóàÎäîÏßÄ ÌôïÏù∏
      if (currentTimeZone !== timeZoneInfo.timeZone || currentOffset !== timeZoneInfo.offset) {
        console.log('üåç ÏãúÍ∞ÑÎåÄ Î≥ÄÍ≤Ω Í∞êÏßÄ:', {
          Ïù¥Ï†Ñ: `${timeZoneInfo.timeZone} (Ïò§ÌîÑÏÖã: ${timeZoneInfo.offset})`,
          ÌòÑÏû¨: `${currentTimeZone} (Ïò§ÌîÑÏÖã: ${currentOffset})`,
        });

        setTimeZoneInfo({
          timeZone: currentTimeZone,
          offset: currentOffset,
          changed: true,
        });

        // Î≥ÄÍ≤Ω ÌîåÎûòÍ∑∏Î•º Ïû†Ïãú ÌõÑ Î¶¨ÏÖã
        setTimeout(() => {
          setTimeZoneInfo(prev => ({ ...prev, changed: false }));
        }, 1000);
      }
    } catch (error) {
      console.warn('ÏãúÍ∞ÑÎåÄ Í∞êÏßÄ Ïã§Ìå®:', error);
    }
  }, [timeZoneInfo.timeZone, timeZoneInfo.offset]);

  useEffect(() => {
    if (typeof window === 'undefined') return;

    // 1. Î∏åÎùºÏö∞Ï†Ä Ìè¨Ïª§Ïä§ Ïù¥Î≤§Ìä∏ - Îã§Î•∏ Ïï±ÏóêÏÑú ÎèåÏïÑÏò¨ Îïå Ï≤¥ÌÅ¨
    const handleFocus = () => {
      setTimeout(checkTimeZone, 100); // ÏïΩÍ∞ÑÏùò ÏßÄÏó∞ÏùÑ ÎëêÏñ¥ ÏãúÏä§ÌÖú Î≥ÄÍ≤Ω Ï†ÅÏö© ÏãúÍ∞Ñ ÌôïÎ≥¥
    };

    // 2. Í∞ÄÏãúÏÑ± Î≥ÄÍ≤Ω Ïù¥Î≤§Ìä∏ - ÌÉ≠Ïù¥ Îã§Ïãú ÌôúÏÑ±ÌôîÎê† Îïå Ï≤¥ÌÅ¨
    const handleVisibilityChange = () => {
      if (document.visibilityState === 'visible') {
        setTimeout(checkTimeZone, 100);
      }
    };

    // 3. ÌéòÏù¥ÏßÄ Ìè¨Ïª§Ïä§ Ïù¥Î≤§Ìä∏
    const handlePageFocus = () => {
      setTimeout(checkTimeZone, 100);
    };

    // 4. Ï£ºÍ∏∞Ï†Å Ï≤¥ÌÅ¨ (5Î∂ÑÎßàÎã§)
    const intervalId = setInterval(checkTimeZone, 5 * 60 * 1000);

    // Ïù¥Î≤§Ìä∏ Î¶¨Ïä§ÎÑà Îì±Î°ù
    window.addEventListener('focus', handleFocus);
    window.addEventListener('pageshow', handlePageFocus);
    document.addEventListener('visibilitychange', handleVisibilityChange);

    // Ï†ïÎ¶¨ Ìï®Ïàò
    return () => {
      window.removeEventListener('focus', handleFocus);
      window.removeEventListener('pageshow', handlePageFocus);
      document.removeEventListener('visibilitychange', handleVisibilityChange);
      clearInterval(intervalId);
    };
  }, [checkTimeZone]);

  return {
    timeZone: timeZoneInfo.timeZone,
    offset: timeZoneInfo.offset,
    changed: timeZoneInfo.changed,
    checkTimeZone,
  };
}

/**
 * ÏãúÍ∞ÑÎåÄ Î≥ÄÍ≤Ω Í∞êÏßÄ Ï†ÑÏö© ÌõÖ (Í∞ÑÎã®Ìïú Î≤ÑÏ†Ñ)
 */
export function useTimeZone() {
  const { timeZone, changed } = useTimeZoneDetection();
  
  return {
    timeZone,
    changed,
  };
} 